import { createStore } from 'vuex'
import PostService from '../PostService'
// import axios from 'axios'
export default createStore({
  state: {
    status: 'Store Online',
    content: [],
    specific: '',
    flag: false,
    errarr: [],
    spin: { val: false },
    errF: false,
    userStatus: true,
    remKey: false,
  },
  mutations: {
    getContent: (state, response) => (state.content = response),
    addContent: (state, response) => {
      // state.content.push(response)
      console.log(state, response)
    },
    removeContent: (state, id) =>
      (state.content = state.content.filter((content) => content.id != id)),
    updateContent: (state, { id, text }) => {
      const arr = JSON.parse(JSON.stringify(state.content))
      const index = arr.findIndex((todo) => todo._id === id)
      state.content.splice(index, 1, text)
    },
    specificContent: (state, res) => {
      // let arr = JSON.parse(JSON.stringify(state.content))
      // const index = arr.findIndex((todo) => todo._id === id)
      // console.log(res.text)
      state.specific = res.text
    },
    setSpinState: (state, res) => {
      state.spin.val = res
    },
    pushError: (state, res) => {
      console.log(state, res)
      state.errarr.push(res)
      state.errF = true
    },
    resetError: (state) => {
      state.errarr = []
      state.errF = false
    },
    userStatus: (state, res) => {
      state.userStatus = res
    },
    remFn: (state, index) => {
      state.content[index].rem = !state.content[index].rem
      console.log(state.content[index].rem)
    },
  },
  actions: {
    async getContent({ commit }) {
      commit('setSpinState', true)
      const response = await PostService.getPosts()
      commit('getContent', response)
      commit('setSpinState', false)
    },
    async addContent({ commit }, text) {
      commit('setSpinState', true)
      const response = await PostService.insertPost(text)
      console.log(response)
      commit('addContent', text)
      commit('setSpinState', false)
    },
    async removeContent({ commit }, id) {
      commit('setSpinState', true)
      await PostService.deletePost(id)
      commit('removeContent', id)
      commit('setSpinState', false)
    },
    async updateContent({ commit }, { id, text }) {
      commit('setSpinState', true)
      await PostService.updatePost(id, text)
      commit('updateContent', { id, text })
      commit('setSpinState', false)
    },
    async specificContent({ commit }, id) {
      commit('setSpinState', true)
      const response = await PostService.getPosts()
      const index = response.findIndex((todo) => todo._id === id)
      console.log(response)
      commit('specificContent', response[index])
      commit('setSpinState', false)
    },
    pushError({ commit }, code) {
      commit('pushError', code)
    },
    resetError({ commit }) {
      commit('resetError')
    },
    userStatus({ commit }) {
      commit('userStatus', navigator.onLine ? true : false)
    },
    async remFn({ commit }, id) {
      const response = await PostService.getPosts()
      const index = response.findIndex((todo) => todo._id === id)
      commit('remFn', index)
    },
    // async specificContent({ commit }, id) {
    //   commit('setSpinState', true)
    //   const response = await PostService.getPosts()
    //   // console.log(response)
    //   if (response) {
    //     commit('specificContent', id)
    //     commit('setSpinState', false)
    //   }
    // },
  },
  getters: {
    allContent: (state) => state.content,
    someContent: (state) => state.specific,
    getEA: (state) => state.errarr,
    getSpin: (state) => state.spin,
    getErr: (state) => state.errarr,
    getErrF: (state) => state.errF,
    getUserStatus: (state) => state.userStatus,
    getRemKey: (state) => state.remKey,
  },
  modules: {},
})
