import axios from 'axios'
// const url = 'http://localhost:3000/api/routes' //5005
const url = 'api/routes/'

class PostService {
  // Get
  static getPosts() {
    return new Promise((resolve, reject) => {
      axios
        .get(url)
        .then((res) => {
          const data = res.data
          // console.log(...data)
          resolve(
            data.map((post) => ({
              ...post,
            }))
          )
        })
        .catch((err) => {
          reject(err)
        })
    })
  }
  // Create
  static insertPost(text) {
    // console.log('s')
    return axios.post(url, { text: text, rem: true })
  }
  // Delete
  static deletePost(id) {
    // console.log(`${url}/${id}`)
    return axios.delete(`${url}/${id}`)
  }
  //update
  //============
  static updatePost(id, text) {
    return axios.put(`${url}/${id}`, { text: text })
  }
  //==============
}
export default PostService
