<template>
  <div class="root">
    <div class="folder">
      <div class="debug">
        <div></div>
      </div>
      <div class="hero">
        <div class="info-box"></div>
        <div class="preview">
          <div class="prebox">
            <h1 class="og">{{ someContent }}</h1>
          </div>
        </div>
        <div class="optional">
          <div v-if="getErrF" class="stopbox">
            {{ getErr }}
          </div>
          <div style="background-color: white" v-else class="deco"></div>
        </div>
        <div class="content-1">
          <div class="ipb">
            <input type="text" v-model="itext" placeholder="Add new task" />
          </div>
          <div class="optbtn">
            <div class="subbtn" id="sub">
              <!-- <router-link :to="{ name: 'PostComponent' }"> -->
              <button
                type="submit"
                value="submit"
                class="btn"
                id="updb"
                @click="onSubmitUpdate(itext), reload"
              >
                Update
              </button>
              <!-- </router-link> -->
            </div>
            <div class="delbtn" id="del">
              <router-link :to="{ name: 'PostComponent' }">
                <button
                  type="submit"
                  value="submit"
                  class="btn"
                  id="delb"
                  @click="onSubmitDelete"
                >
                  Delete
                </button>
              </router-link>
            </div>
          </div>
        </div>
        <div class="home">
          <router-link :to="{ name: 'PostComponent' }">
            <button @click="reload" class="hbtn">Home</button>
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<!-- <button class="Ubtn" @click="select(post)">Update</button> -->

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  data() {
    return {
      itext: '',
      id: this.$route.params.id,
      // text: 'Great INDIAN lAUGHTER SHOW',
      show: this.allContent,
    }
  },
  methods: {
    ...mapActions([
      'getContent',
      'addContent',
      'removeContent',
      'updateContent',
      'specificContent',
      'checkLocal',
      'pushError',
      'resetError',
    ]),
    reload() {
      window.location.reload
    },
    emptyString(res) {
      if (res == '' || res == null) {
        return true
      } else {
        return false
      }
    },
    onSubmitUpdate() {
      this.resetError()
      if (this.emptyString(this.itext)) {
        console.log('err')
        this.pushError('Code$1{"Empty Task Not Allowed"}')
      } else {
        this.updateContent({ id: this.id, text: this.itext })
        this.specificContent(this.id)
        this.itext = ''
        this.resetError()
      }
    },
    onSubmitDelete() {
      this.removeContent(this.$route.params.id)
      console.log(this.show)
    },
  },
  // computed: mapGetters(['allContent']),
  computed: {
    ...mapGetters(['allContent', 'someContent', 'getErr', 'getErrF']),
  },
  created() {
    this.getContent()
    this.specificContent(this.id)
    this.resetError()
  },
}
</script>
