      <div class="pcSuper">
        <div class="post-container">
          <div
            class="post"
            v-for="post in allContent"
            v-bind:item="post"
            v-bind:key="post._id"
            @click="work(post._id)"
          >
            <div class="content">
              <!-- @click="post.rem = !post.rem" -->
              <p :class="[post.rem ? '' : 'text-s', 'text']">
                {{ post.text }}
              </p>
            </div>
