<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'PostComponent',
  components: {},
  computed: mapGetters([
    'allContent',
    'someContent',
    'everything',
    'getErr',
    'getErrF',
    'getUserStatus',
    'getRemKey',
  ]),
  data() {
    return {
      text: '',
      toggle: false,
    }
  },
  async created() {
    try {
      this.getContent()
    } catch (err) {
      this.pushError(err)
    }
    setInterval(() => {
      this.userStatus()
      this.resetError()
    }, 3000)
    this.resetError()
  },
  methods: {
    ...mapActions([
      'getContent',
      'addContent',
      'removeContent',
      'updateContent',
      'specificContent',
      'checkLocal',
      'pushError',
      'resetError',
      'userStatus',
      'remFn',
    ]),
    emptyString(res) {
      if (res == '' || res == null) {
        return true
      } else {
        return false
      }
    },
    work(id) {
      this.remFn(id)
    },
    async createPost() {
      this.resetError()
      if (this.emptyString(this.text)) {
        this.pushError('Code$1{"Empty Task Not Allowed"}')
      } else {
        this.addContent(this.text)
        this.text = ''
        this.getContent()
      }
    },
    async deletePost(id) {
      this.removeContent(id)
      this.getContent()
      this.resetError()
    },
  },
}
</script>

