import { createRouter, createWebHistory } from 'vue-router'
import About from '../components/AboutUs.vue'
import PostComponent from '../components/PostComponent.vue'
import EditMe from '../components/EditMe.vue'
import NotFound from '../components/NotFound.vue'
//An array which defines different routes
// in order to change any path name we can channe path:'' and not change its name thus helping to not route each webapge
const routes = [
  {
    path: '/home',
    name: 'PostComponent',
    component: PostComponent,
  },
  {
    path: '/about',
    name: 'About',
    component: About,
  },
  {
    path: '/edit/:id',
    name: 'edit',
    component: EditMe,
  },
  // redirect
  {
    path: '',
    redirect: '/home',
  },
  // // 404 not found
  { path: '/:catchAll(.*)', name: 'notfound', component: NotFound },
]
// used for forward and backward on website
const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
})

export default router
